/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: linear-gradient(45deg, #f3e2c7 2%, #f3e2c7 4%, #9ab7bc 23%, #c19e67 39%, #95af90 74%, #f3e2c7 92%, #e9d4b3 100%);
  color: black;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.contenedor h1 {
  color: white;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.fondoLogin {
  background-image: url("../img/login.jpg");
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.fondoRegistro {
  background: linear-gradient(45deg, #f3e2c7 2%, #f3e2c7 4%, #9ab7bc 23%, #c19e67 39%, #95af90 74%, #f3e2c7 92%, #e9d4b3 100%);
  height: 100vh;
}

.form {
  position: relative;
  margin: 0 auto;
  max-width: 50rem;
  margin-top: 20%;
}
.form .datos {
  position: relative;
  padding: 5rem;
}
.form .datos h2 {
  color: #707070;
}
.form .bglila {
  opacity: 0.8;
  position: absolute;
  background-color: #f1f0d6;
  height: 100%;
  width: 100%;
  border-radius: 6%;
}

.login {
  display: flex;
  flex-direction: column;
}
.login input {
  color: #707070;
  margin-bottom: 2.5rem;
  border: none;
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 10rem;
}

button {
  text-align: center;
  margin: 0.4rem 0 0.4rem;
  padding: 1rem;
  background-color: #f1f0d6;
  border: none;
  color: #707070;
  font-weight: 600;
  border-radius: 6rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  button {
    margin: 0;
  }
}
button:hover {
  background-color: #b3b399;
  color: white;
}

.olvidarUsuario {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.olvidarUsuario a {
  margin-top: 2.3rem;
  padding: 1rem;
  color: white;
  background-color: rgba(255, 0, 0, 0.6);
  border-radius: 5rem;
}
.olvidarUsuario p {
  color: #707070;
}
.olvidarUsuario p span {
  cursor: pointer;
  font-weight: 600;
}

.confirmado {
  margin-top: 20%;
}

.alert {
  display: flex;
  justify-content: space-around;
}
.alert .alerta {
  text-align: center;
  padding: 1.2rem;
  border-radius: 2rem;
  width: 90%;
  margin: 1.1rem 0;
}
.alert .error {
  background-color: #cb0000;
}
.alert .exito {
  background-color: #329f00;
}

.header {
  margin: 3rem 0 15rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header a {
  color: white;
  font-size: 2rem;
}
.header .logo {
  height: 10rem;
  display: flex;
  align-items: center;
}
.header .logo p {
  margin-left: 2.8rem;
}
.header .logo img {
  width: 9rem;
  height: 9rem;
}
.header i {
  font-size: 6rem;
}

.crear {
  background: linear-gradient(45deg, #f3e2c7 2%, #f3e2c7 4%, #9ab7bc 23%, #c19e67 39%, #95af90 74%, #f3e2c7 92%, #e9d4b3 100%);
}

.funciones {
  max-width: 80rem;
  margin: 0 auto;
}
.funciones .grid {
  margin-bottom: 5rem;
  margin: 0;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  justify-items: center;
}
.funciones .grid .crearBoleta {
  background-image: url("../img/funciones.webp");
  background-position: center;
  background-size: cover;
}
.funciones .grid .verReportes {
  background-image: url("../img/funciones2.webp");
  background-position: center;
  background-size: cover;
}
.funciones .grid .productos {
  width: 90%;
  background-image: url("../img/funciones3.webp");
  background-position: center;
  background-size: cover;
}
.funciones .grid .cliente {
  background-image: url("../img/funciones4.webp");
  background-position: center;
  background-size: cover;
}
.funciones .grid .itemGrid {
  text-align: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 10rem;
}
.funciones .grid .itemGrid a {
  color: white;
  font-size: 4.8rem;
  font-weight: 800;
}
.funciones .grid .itemGrid :hover {
  font-size: 5rem;
  color: #cbdfa4;
}

.footer {
  background-color: black;
  display: flex;
  flex-direction: column;
}
.footer .contenido {
  margin: 0 10% 0 10%;
}
.footer .contenido .logo {
  margin: 5rem 0rem 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: solid 1px white;
}
.footer .contenido .logo img {
  width: 7rem;
  height: 7rem;
  margin-bottom: 5rem;
}

.texto {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5rem;
}
.texto span {
  font-weight: 600;
}

.main-CrearBoleta {
  max-width: 50%;
  margin: 0 auto;
}

.formulario {
  background-image: url(../../../src/img/contacto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  padding-top: 2rem;
}

.i-personal {
  margin-bottom: 3rem;
  position: relative;
}
.i-personal .bg-verde {
  z-index: 1;
  position: absolute;
  background-color: #419741;
  opacity: 0.4;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
}
.i-personal .bg-black {
  z-index: 1;
  position: absolute;
  background-color: #d8c1c1;
  opacity: 0.4;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
}
.i-personal .bg-azul {
  z-index: 1;
  position: absolute;
  background-color: #5050e0;
  opacity: 0.4;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
}
.i-personal .datoC {
  position: relative;
  padding: 3rem;
  z-index: 3;
  top: 0;
  left: 0;
  right: 0;
}
.i-personal a {
  position: relative;
  z-index: 2;
  text-align: center;
  display: block;
  width: 30%;
  margin-top: 1.3rem;
  background-color: #682d2b;
  padding: 0.8rem;
  border-radius: 2rem;
  border-color: none;
  text-transform: uppercase;
  font-size: 2.8rem;
  text-decoration: none;
  font-weight: 600;
  color: white;
}
.i-personal input {
  margin-bottom: 0.2rem;
  border-radius: 2;
}

.dato {
  z-index: 2;
  flex-direction: column;
  display: flex;
}

.cliente {
  display: flex;
  flex-direction: column;
}
@media (min-width: 480px) {
  .cliente {
    display: grid;
    grid-template-columns: 60% 39%;
    align-items: flex-end;
    column-gap: 1.2rem;
  }
}

@media (min-width: 480px) {
  .datosCliente {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 1rem;
  }
}

.navegacion .login {
  color: green;
}
.navegacion .login:hover {
  color: red;
}

.datoC p {
  color: white;
}

.dato {
  color: white;
}
.dato label {
  font-size: 1.9rem;
  font-weight: 600;
  margin-top: 1.2rem;
}
.dato textarea {
  opacity: 0.9;
  border-radius: 1rem;
  padding: 1rem 1rem;
  border-width: 0rem;
}
.dato input {
  opacity: 0.9;
  border-radius: 1rem;
  padding: 1rem 1rem;
  border-width: 0rem;
}

.checkbox {
  display: grid;
  gap: 4rem;
  grid-template-columns: repeat(2, 1fr);
}

.date-time {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
}

.btn {
  width: 90%;
  border: none;
  margin-left: auto;
  margin-bottom: 2rem;
}

.visible {
  visibility: visible;
}

.oculto {
  visibility: hidden;
  position: fixed;
}

.tabla {
  width: 100%;
}
.tabla .producto_lista {
  color: black;
}
.tabla .producto_lista th {
  border: 6px solid #fcfcfc;
  padding: 0.5rem;
  background-color: #e3d9be;
  text-align: center;
}